.main {
  position: relative;
  background-image: url("../img/main-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 190px 0 50px;
  overflow: hidden;
  &__desc {
    position: relative;
    z-index: 3;
    max-width: 640px;
  }
  &__title {
    max-width: 615px;
    font-size: 48px;
    line-height: 130%;
    font-weight: 600;
  }
  &__subtitle {
    font-size: 20px;
    line-height: 120%;
    margin-top: 30px;
  }
  &__btn {
    height: 78px;
    padding: 5px 78px;
    margin-top: 30px;
    span {
      font-size: 18px;
    }
  }
  &__items {
    display: flex;
    align-items: start;
    gap: 60px;
    margin-top: 60px;
  }
  &__item {
    position: relative;
    max-width: 180px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 5px;
    &:not(:last-child) {
      &:after {
        content: '';
        position: absolute;
        top: -16px;
        right: -30px;
        width: 1px;
        height: 115px;
        background: #F1F1F1;
        pointer-events: none;
      }
    }
    &-icon {
      width: 42px;
      height: 42px;
      display: flex;
      justify-content: center;
      img {
        max-width: 100%;
        max-height: 100%;
      }
    }
    &-text {
      font-size: 18px;
      line-height: 150%;
      font-weight: 500;
    }
  }
  &__img {
    display: none;
    img {
      display: block;
    }
  }
}
.info {
  padding: 80px 0;
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 20px / 2));
    gap: 20px;
  }
  &__item {
    &--center {
      display: flex;
      justify-content: center;
      flex-direction: column;
    }
    &--padding {
      padding-left: 40px;
    }
    &--flex{
      display: flex;
      gap: 60px;
      .info__item-img {
        width: 51%;
      }
      .info__item-desc {
        width: 49%;
        display: flex;
        justify-content: center;
        flex-direction: column;
      }
      .info__item-text {
        max-width: 525px;
      }
    }
    &-title {
      span {
        color: #0C92BF;
      }
    }
    &-text {
      margin-top: 20px;
      p {
        font-size: 18px;
        line-height: 150%;
      }
    }
    &-text2 {
      margin-bottom: 20px;
      p {
        font-size: 20px;
        line-height: 150%;
        font-weight: 500;
      }
    }
    &-img {
      height: 100%;
      display: block;
      img {
        width: 100%;
        border-radius: 10px;
      }
    }
    &-list {
      position: relative;
      max-width: 507px;
      display: grid;
      grid-template-columns: 100%;
      gap: 20px;
      padding-left: 20px;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 1px;
        height: 100%;
        background: #D1D1D1;
        pointer-events: none;
      }
      &:before {
        content: '';
        position: absolute;
        z-index: 1;
        top: 0;
        left: -10px;
        width: 20px;
        height: 20px;
        background: #0C92BF;
        border-radius: 50%;
        pointer-events: none;
      }
      li {
        display: flex;
        align-items: center;
        gap: 30px;
        font-size: 16px;
        line-height: 150%;
      }
    }
    &-list2 {
      max-width: 380px;
      gap: 65px;
      margin-top: 28px;
      &:before {
        animation: down-up 5s infinite;
      }
      li {
        gap: 60px;
      }
    }
    &-number {
      font-size: 60px;
      line-height: 105%;
      font-weight: 500;
      color: #0C92BF;
    }
    &-year {
      font-size: 18px;
      font-weight: 600;
    }
    &-slider {
      position: relative;
    }
    .swiper-slide {
      height: auto;
      img {
        height: auto;
        border-radius: 5px;
      }
    }
    .swiper-pagination {
      bottom: -18px;
      &-bullet {
        background: #F0F0F0;
        opacity: 1 !important;
      }
      &-bullet-active {
        background: #0C92BF;
      }
    }
    .year-2022 {
      animation: color 5s infinite;
    }
    .year-2023 {
      animation: color2 5s infinite;
    }
    .year-2024 {
      animation: color3 5s infinite;
    }
  }
}
.info2 {
  position: relative;
  background-image: url("../img/info2-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 70px 0;
  &__top {
    position: relative;
    z-index: 2;
    display: flex;
    align-items: center;
    gap: 24px;
  }
  &__title {
    max-width: 765px;
    color: #fff;
  }
  &__subtitle {
    max-width: 275px;
    flex-shrink: 0;
    font-size: 16px;
    line-height: 150%;
    color: #C3C3C3;
  }
  &__cards {
    position: relative;
    z-index: 2;
    max-width: 840px;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 32px / 2));
    gap: 32px;
    margin-top: 165px;
  }
  .triangle {
    position: absolute;
    z-index: 2;
    top: -15px;
    left: 50%;
    transform: translate(-50%, 0);
    width: 74px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__img {
    position: absolute;
    z-index: 1;
    right: 0;
    bottom: 0;
    max-width: 877px;
    &:after {
      content: '';
      position: absolute;
      right: 90px;
      bottom: 0;
      width: 500px;
      height: 500px;
      background: #FFCC33;
      filter: blur(323.3px);
      border-radius: 50%;
      pointer-events: none;
    }
    img {
      position: relative;
      z-index: 2;
      width: 100%;
    }
  }
}
