.partners {
  position: relative;
  z-index: 2;
  background: #fff;
  padding: 80px 0 40px;
  overflow: hidden;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
  }
  &__desc {
    width: 445px;
  }
  &__img {
    width: calc(100% - 469px);
    background: #fff;
    box-shadow: 0px 4px 89.2px 0px rgba(198, 198, 198, 0.25);
    border-radius: 10px;
    padding: 25px 40px;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  &__text {
    margin-top: 20px;
    p {
      font-size: 18px;
      line-height: 150%;
    }
  }
}
.map-section {
  padding: 40px 0;
  &__title {
    text-align: center;
    span {
      color: #0C92BF;
    }
  }
  &__subtitle {
    text-align: center;
    font-size: 24px;
    line-height: 120%;
    margin-top: 8px;
  }
  &__img {
    position: relative;
    margin-top: 40px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180.00deg, rgba(255, 255, 255, 0.71),rgba(255, 255, 255, 0) 29.621%);
      pointer-events: none;
    }
    img {
      width: 100%;
    }
  }
}
.video-section {
  padding: 40px 0;
  &__title {
    text-align: center;
  }
  .video-wrap {
    position: relative;
    display: block;
    margin-top: 40px;
    overflow: hidden;
    &:after {
      content: '';
      position: absolute;
      top: -15px;
      left: 50%;
      transform: translate(-50%, 0);
      width: 74px;
      height: 43px;
      background-image: url("../img/triangle.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
      pointer-events: none;
    }
    &:hover {
      .video img {
        transform: scale(1.125);
      }
    }
  }
  .video {
    position: relative;
    min-height: 365px;
    border-radius: 10px;
    padding-bottom: 50%;
    overflow: hidden;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 10px;
      transition: all 0.3s;
    }
  }
  .video__play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 110px;
    height: 110px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #000;
    border-radius: 10px;
    svg {
      max-width: 60px;
      height: auto;
    }
    svg path {
      fill: #fff;
    }
  }
}
.info3 {
