.footer {
  background: #363843;
  padding: 60px 0 45px;
  color: #fff;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  &__logo {
    width: 110px;
    flex-shrink: 0;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 14px;
    line-height: 120%;
    color: #C3C3C3;
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 50px;
    li a {
      font-size: 16px;
      line-height: 120%;
    }
    li a:hover {
      color: #FFCC33;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  &__social {
    width: 39px;
    height: 39px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #0C92BF;
    border-radius: 50%;
    &:hover {
      background: #FFCC33;
      svg path {
        fill: #363843;
      }
    }
    svg {
      max-width: 20px;
      height: auto;
    }
    svg path {
      transition: all 0.3s;
    }
  }
  &__items {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 40px;
    margin-top: 40px;
  }
  &__item {
    &-title {
      display: flex;
      align-items: center;
      gap: 8px;
      i {
        max-width: 16px;
        flex-shrink: 0;
        svg {
          width: auto;
          height: auto;
        }
      }
      span {
        font-size: 16px;
        line-height: 150%;
        font-weight: 500;
      }
      a {
        display: inline-block;
      }
    }
    &-text {
      font-size: 14px;
      line-height: 150%;
      color: #C3C3C3;
      margin-top: 5px;
    }
  }
  .phone-title {
    white-space: nowrap;
  }
  &__text2 {
    display: flex;
    gap: 30px;
    margin-top: 40px;
    p {
      font-size: 14px;
      line-height: 120%;
      color: #C3C3C3;
    }
    a:hover {
      color: #fff;
    }
  }
}

@media screen and (max-width: 1200px) {
  .footer {
    &__top {
      gap: 30px;
    }
    &__list {
      gap: 30px;
      li a {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    padding: 40px 0;
    &__top {
      flex-wrap: wrap;
      gap: 40px 30px;
    }
    &__list {
      width: 100%;
      order: 2;
      justify-content: center;
      gap: 90px;
      li {
        font-size: 16px;
      }
    }
    &__text2 {
      justify-content: center;
    }
  }
}

@media screen and (max-width: 767px) {
  .footer {
    &__list {
      justify-content: space-between;
      gap: 20px;
    }
    &__items {
      gap: 20px;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &__top {
      flex-direction: column;
      gap: 30px;
    }
    &__group {
      gap: 15px;
    }
    &__logo {
      width: 90px;
    }
    &__text {
      font-size: 12px;
    }
    &__list {
      max-width: 210px;
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
      gap: 15px 10px;
    }
    &__items {
      gap: 30px;
      flex-direction: column;
      align-items: center;
      text-align: center;
      margin-top: 30px;
    }
    &__item {
      &-title {
        justify-content: center;
      }
    }
    &__text2 {
      flex-direction: column;
      align-items: center;
      gap: 15px;
      margin-top: 30px;
      p {
        font-size: 14px;
      }
    }
  }
}