.header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  padding: 30px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
  }
  &__left {
    position: relative;
    z-index: 10;
    display: flex;
    align-items: center;
    gap: 20px;
  }
  &__logo {
    width: 110px;
    flex-shrink: 0;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 14px;
    line-height: 120%;
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 50px;
    li a {
      font-size: 16px;
      line-height: 120%;
    }
    li a:hover {
      text-shadow: 0 0 .65px #000, 0 0 .65px #000;
    }
  }
}
.burger {
  position: relative;
  z-index: 10;
  width: 50px;
  height: 50px;
  flex-shrink: 0;
  display: none;
  background: linear-gradient(39.73deg, rgb(255, 204, 51) -54.113%,rgb(250, 228, 161) 48.377%,rgb(255, 204, 51) 159.184%);
  box-shadow: 0px 2px 0px 0px rgb(207, 183, 99);
  border-radius: 4px;
  cursor: pointer;
  span {
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    height: 2px;
    background: #363843;
    border-radius: 4px;
    transition: all 0.3s;
  }
  span:nth-child(1) {
    top: 18px;
    width: 18px;
  }
  span:nth-child(2) {
    top: 22px;
    width: 12px;
  }
  span:nth-child(3) {
    bottom: 22px;
    width: 18px;
  }
  span:nth-child(4) {
    bottom: 18px;
    width: 12px;
  }
  &--active {
    span:nth-child(1) {
      top: 24px;
      transform: translate(-50%, 0) rotate(-45deg);
    }
    span:nth-child(2),
    span:nth-child(4) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 24px;
      transform: translate(-50%, 0) rotate(45deg);
    }
  }
}
.menu-bg {
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  background: rgba(54, 56, 67, 0.8);
  opacity: 0;
  pointer-events: none;
  transition: all 0.35s;
  &--active {
    opacity: 1;
    pointer-events: auto;
  }
}

@media screen and (max-width: 1050px) {
  .header {
    &__inner {
      gap: 30px;
    }
    &__list {
      gap: 30px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__inner {
      flex-direction: column;
    }
    &__right {
      width: 100%;
    }
    &__list {
      justify-content: space-between;
    }
  }
}

@media screen and (max-width: 767px) {
  .header {
    &__inner {
      flex-direction: row;
      gap: 10px;
    }
    &__right {
      position: fixed;
      z-index: 7;
      top: 0;
      left: 0;
      width: 100%;
      height: auto;
      max-height: 100%;
      background: #fff;
      padding: 140px 20px 70px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
      &:after {
        content: '';
        position: fixed;
        top: -10px;
        left: 0;
        width: 100%;
        height: 108px;
        background: #fff;
        pointer-events: none;
      }
    }
    &__list {
      flex-direction: column;
      text-align: center;
      gap: 20px;
      li {
        width: 100%;
      }
      li:not(:last-child) {
        border-bottom: 1px solid #DFDFDF;
        padding-bottom: 20px;
      }
    }
  }
  .burger {
    display: block;
  }
  .menu-bg {
    display: block;
  }
}

@media screen and (max-width: 576px) {
  .header {
    padding: 24px 0;
    &__left {
      gap: 15px;
    }
    &__logo {
      width: 90px;
    }
    &__text {
      font-size: 11.8px;
    }
  }
}

@media screen and (max-width: 359px) {
  .header {
    &__logo {
      width: 80px;
    }
  }
}