.wrapper {
  overflow: hidden;
}
.yellow-btn {
  background: linear-gradient(47.14deg, rgb(255, 204, 51) -14.37%,rgb(250, 228, 161) 45.008%,rgb(255, 204, 51) 109.204%);
  background-color: #FFD75E;
  box-shadow: 0px 3.46px 0px 0px rgb(207, 183, 99);
  border-radius: 5px;
  &:hover {
    background: #FFD75E;
    box-shadow: unset;
  }
  span {
    line-height: 120%;
    font-weight: 500;
    color: #363843;
  }
}
.h2 {
  font-size: 32px;
  line-height: 150%;
  font-weight: 600;
}
.arrows {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 0);
  width: 100%;
  min-width: 2000px;
  pointer-events: none;
  img {
    width: 100%;
    display: block;
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(54, 56, 67, 0.8);
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    width: 100%;
    max-width: 770px;
    background: #fff;
    border-radius: 10px;
    margin: 0 auto;
    padding: 40px;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      max-width: calc(100% - 30px);
    }
  }
  &__close {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 17px;
    background: transparent;
    padding: 0;
    &:hover {
      svg path {
        stroke: #0C92BF;
      }
    }
    svg {
      width: 100%;
      height: auto;
    }
    svg path {
      transition: all 0.3s;
    }
  }
  &__title {
    max-width: 500px;
    text-align: center;
    font-size: 23px;
    line-height: 150%;
    font-weight: 600;
    margin: 0 auto;
  }
  &__form {
    margin-top: 15px;
    &-grid {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
      gap: 15px;
    }
    &-group {
      input {
        width: 100%;
        height: 50px;
        border: 1px solid #D3D3D3;
        border-radius: 5px;
        padding: 0 18px;
        font-size: 15px;
        line-height: 120%;
        &::placeholder {
          color: #7C7E89;
        }
      }
      textarea {
        width: 100%;
        height: 120px;
        border: 1px solid #D3D3D3;
        border-radius: 5px;
        padding: 18px;
        font-size: 15px;
        line-height: 120%;
        resize: none;
        &::placeholder {
          color: #7C7E89;
        }
      }
    }
    &-group--big {
      grid-column: 1/3;
    }
    &-btn {
      min-width: 150px;
      height: 50px;
      padding: 5px 25px;
      margin: 15px auto 0;
      span {
        font-size: 15px;
      }
    }
    &-info {
      text-align: center;
      font-size: 15px;
      line-height: 150%;
      color: #7C7E89;
      margin-top: 15px;
      a {
        text-decoration: underline;
        color: #0C92BF;
      }
    }
    &-bot {
      max-width: 260px;
      margin: 15px auto 0;
      img {
        width: 100%;
      }
    }
  }
}
@keyframes down-up {
  0% {
    top: 0;
  }
  100% {
    top: 100%;
  }
}
@keyframes color {
  0% {
    color: #0C92BF;
  }
  100% {
    color: #363843;
  }
}
@keyframes color2 {
  0% {
    color: #363843;
  }
  60% {
    color: #0C92BF;
  }
  100% {
    color: #363843;
  }
}
@keyframes color3 {
  0% {
    color: #363843;
  }
  100% {
    color: #0C92BF;
  }
}

@media screen and (max-width: 576px) {
  .h2 {
    font-size: 20px;
  }
  .modal {
    &__dialog {
      padding: 40px 30px;
    }
    &__title {
      font-size: 20px;
    }
    &__form {
      &-grid {
        grid-template-columns: 100%;
      }
      &-group--big {
        grid-column: 1;
      }
    }
  }
}